.print-template-container {
  // position: relative;
  width: 1150px;
  height: 100%;
  // height: 833px;
  page-break-after: always;
  break-after: page;
  padding: 4px;

  & * {
    font-family: Roboto, sans-serif !important;
  }

  .program-client-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 8px;

    .info-wrapper {
      display: flex;
      gap: 8px;
      align-items: center;

      span:first-of-type {
        font-size: 18px;
        font-weight: 500;
      }

      span {
        font-size: 18px;
        font-weight: 400;
      }
    }
  }

  .print-grid-table {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: 360px;
    // border: 4px solid black;
    gap: 2px;

    .exercise-cell {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;
      background-color: white;
      outline: 2px solid black;
      padding-bottom: 4px;

      // &:nth-of-type(1) {
      //   border-right: 4px solid black;
      //   border-bottom: 4px solid black;
      // }

      // &:nth-of-type(2) {
      //   border-right: 4px solid black;
      //   border-bottom: 4px solid black;
      // }

      // &:nth-of-type(3) {
      //   border-bottom: 4px solid black;
      // }

      // &:nth-of-type(4) {
      //   border-right: 4px solid black;
      // }

      // &:nth-of-type(5) {
      //   border-right: 4px solid black;
      // }

      .title-img-container {
        flex-grow: 1;
        display: flex;
        flex-direction: column;
        width: 100%;
        align-self: flex-start;
        min-height: 170px;

        .title {
          padding: 4px;
          border-bottom: 1px solid black;

          & > span {
            // font-size: 14px;
          }
        }

        .img {
          align-self: center;
          padding: 2px 0;
          object-fit: contain;
          min-height: 150px;
        }
      }

      .exercise-info-wrapper {
        // flex-basis: 150px;
        min-height: 162px;
        width: 100%;
        padding: 0;
        border-top: 1px solid black;
        overflow: hidden;
        min-height: 162px;

        .sets-reps {
          display: flex;
          flex-wrap: wrap;
          gap: 1px;
          width: 100%;
          // line-height: 1.6;
          border-bottom: 1px solid black;

          & span {
            // font-size: 14px;
            line-height: 1;
          }

          & > div {
            flex: 1 1 25%;
            outline: 1px solid black;
            padding: 4px;
            // padding: 0 4px;
          }
        }

        .notes {
          //   min-height: 90px;
          // padding: 4px;
          // padding-bottom: 0;
          // display: -webkit-box;
          // line-clamp: 7;
          // -webkit-line-clamp: 7; /* Restrict to three lines */
          // -webkit-box-orient: vertical;
          // overflow: hidden;
          // text-overflow: ellipsis;

          & > span {
            // font-size: 14px;
          }
        }
      }
    }
  }
  .print-page-number {
    position: absolute;
    bottom: 0;
    right: 0;
  }
}

@media print {
  .print-template-container {
    width: 1200px;
    height: 100%;
    padding: 0.5in; /* Set minimal margins for all sides */

    & * {
      font-family: Roboto, sans-serif !important;
    }

    .program-client-container {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      margin-bottom: 8px;

      .info-wrapper {
        display: flex;
        gap: 8px;
        align-items: center;

        span:first-of-type {
          font-size: 18px;
          font-weight: 500;
        }

        span {
          font-size: 18px;
          font-weight: 400;
        }
      }
    }

    .print-grid-table {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-auto-rows: 360px;
      // border: 4px solid black;
      gap: 4px;

      .exercise-cell {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        background-color: white;
        outline: 4px solid black;

        // &:nth-of-type(1) {
        //   border-right: 4px solid black;
        //   border-bottom: 4px solid black;
        // }

        // &:nth-of-type(2) {
        //   border-right: 4px solid black;
        //   border-bottom: 4px solid black;
        // }

        // &:nth-of-type(3) {
        //   border-bottom: 4px solid black;
        // }

        // &:nth-of-type(4) {
        //   border-right: 4px solid black;
        // }

        // &:nth-of-type(5) {
        //   border-right: 4px solid black;
        // }

        .title-img-container {
          flex-grow: 1;
          display: flex;
          flex-direction: column;
          width: 100%;
          align-self: flex-start;

          .title {
            padding: 0 4px;
            border-bottom: 1px solid black;

            & > span {
              font-size: 14px;
            }
          }

          .img {
            align-self: center;
            padding: 16px;
            object-fit: contain;
            height: 150px;
          }
        }

        .exercise-info-wrapper {
          // flex-basis: 150px;
          height: 142px;
          width: 100%;
          padding: 0;
          border-top: 1px solid black;
          overflow: hidden;

          .sets-reps {
            display: flex;
            flex-wrap: wrap;
            gap: 1px;
            width: 100%;
            line-height: 1.6;
            border-bottom: 1px solid black;

            & span {
              font-size: 14px;
            }

            & > div {
              flex: 1 1 25%;
              outline: 1px solid black;
              padding: 0 4px;
            }
          }

          .notes {
            padding: 4px;
            overflow: hidden;

            & > span {
              font-size: 14px;
            }
          }
        }
      }
    }
  }
}
